    <div class="content">
        <mu-row>
          <mu-col span="12">
            <div class="name" style="font-weight:600;padding:0 16px;">运维详情：</div>
          </mu-col>
          <mu-col span="10" offset="1">
            <!-- {{this.list.dutyremarks}} -->
            <div style="font-size:10px;">
              <textarea
                name="textarea"
                id="textarea"
                v-model="this.list.dutyremarks"
                cols="48"
                rows="10"
                disabled
              ></textarea>
