        </mu-row>
      </div>
      <div class="icon">
        <span style="font-weight:600;font-size:16px;padding:0 16px;">附件：</span>
        <!-- <mu-icon value="attachment" size="25"></mu-icon> -->
      </div>
      <div class="carouselImg1" style="padding:0 24px;">
        <div v-for="(item,index) in list2" :key="index" >
          <img :src="url+item" alt srcset style="width:280px;">
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      token:sessionStorage.getItem("token"),
      list2: [],
      msg: "hello 数据详情",
      id: 0,
      list: [],
      // token: "",
      show: false,
      url: "http://47.112.16.168:8888/"
    };
  },
  created() {
    this.id = this.$route.query.id;
    // this.token = this.$store.state.token;
    this.axios();
    console.log(this.id);
  },
  beforeCreate() {},
  methods: {
    initPictrue(e) {
      console.log("e", e.photo.split(","));
      this.list2 = e.photo.split(",");
    },
    async axios() {
      var url = "http://47.112.16.168:8800/operate/query";
      var that = this;
      var area = null;
      var street = null;
      var status = null;
      var type = null;
      var startTime = null;
      var endTime = null;

      await this.$http({
        method: "post",
        url: url,
        headers: {
          token: that.token
        },
        data: {
          area,
          street,
          status,
          type,
          startTime,
          endTime
        }
      })
        .then(res => {
          console.log(res.data.data);
          // this.list = res.data.data;
          this.list = res.data.data.find(element => element.dutyid == this.id);
          console.log("list", this.list);
          console.log(res.data);
          this.show = true;
          this.initPictrue(this.list);
        })
        .catch(function(error) {
          console.log("出错了", error);
        });
    }
  }
};
</script>
<style scoped>
.carouselImg1 img {
  width: 20%;
}
.carouselImg1 {
  display: flex;
  flex-direction: row;
  width: 98vw;
  padding: 0 10px;
}
.carouselImg1 div {
  width: 25%;
}
.carouselImg1 img {
  width: 99%;
}
.content {
  padding: 5px;
}
.icon {
  height: 40px;
  line-height: 40px;
}
#textarea {
  width: 100%;
  border: none;
}
#controlList mu-list mu-list-item {
  height: 30px;
}
.name{
  font-size: 16px;
  font-weight: 500;
}
.Controlright{
  text-align: right;
}
</style>



